.card {
    width: 220px;
    margin: 10px auto;
    border: 2px solid black;
    background-color: lightgray;
    border-radius: 20px;
  }

#images {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

.type {
    background-color: lightblue;
    width: 150px;
    height: 50px;
    line-height: 50px;
    vertical-align: middle;
    margin: 10px auto;
    border-radius: 5px;
}